"use client";
import { PublicUserDetailsDto } from "@/models/dtos";
import { useState } from "react";

import { useParams } from "next/navigation";
import { useEffect } from "react";

export default function ProfilePage() {
    const params = useParams();
    const id = params.id as string;

    const [user, setUser] = useState<PublicUserDetailsDto | null>(null);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        fetch(`/api/users/${id}`)
            .then(res => res.json())
            .then(data => {
                setUser(data);
                setIsLoading(false);
            })
    }, [id]);

    if (isLoading) {
        return <div>Loading...</div>
    }

    return (
        <>
            <h1 className="text-2xl font-bold text-secondary-700 tracking-wider">Profile</h1>
            <div className="flex flex-col gap-4">
                <div className="flex flex-row gap-4">
                    <img src={"https://picsum.photos/200"} alt="Profile Picture" className="w-16 h-16 rounded-full" />
                    <h1 className="text-2xl font-bold text-secondary-700 tracking-wider">{user?.username}</h1>
                </div>
                <div className="flex flex-col gap-4">
                    <h1 className="text-2xl font-bold text-secondary-700 tracking-wider">Posts</h1>
                </div>
            </div>
        </>
    )
}